import Vue from 'vue'
import VueRouter from 'vue-router'

import Login from '../components/Login.vue'
import Home from '../components/Home.vue'
import ArticleList from '../components/ArticleList.vue'
import CateMana from '../components/CateMana.vue'

import PostArticle from '../components/PostArticle.vue'
import BlogDetail from '../components/BlogDetail.vue'

import BlogShow from '../components/BlogShow.vue'
import blogshowdetail from '../components/blogshowdetail.vue'
Vue.use(VueRouter)

const routes = [
    {
      path:'/',
      component:BlogShow,
      hidden:true
    },

    {
      path:'/blogshowdetail',
      component:blogshowdetail,
      hidden:true
    },
    {
      path: '/login',
      name: '登录',
      hidden: true,
      component: Login
    },{
      path: '/home',
      name: '',
      component: Home,
      hidden: true
    }, {
      path: '/home',
      component: Home,
      name: '文章管理',
      iconCls: 'fa fa-file-text-o',
      children: [
        {
          path: '/articleList',
          name: '文章列表',
          component: ArticleList,
          meta: {
            keepAlive: true
          }
        }, {
          path: '/postArticle',
          name: '发表文章',
          component: PostArticle,
          meta: {
            keepAlive: false
          }
        }, {
          path: '/blogDetail',
          name: '博客详情',
          component: BlogDetail,
          hidden: true,
          meta: {
            keepAlive: false
          }
        }, {
          path: '/editBlog',
          name: '编辑博客',
          component: PostArticle,
          hidden: true,
          meta: {
            keepAlive: false
          }
        }
      ]
    },  {
      path: '/home',
      component: Home,
      name: '栏目管理',
      children: [
        {
          path: '/cateMana',
          iconCls: 'fa fa-reorder',
          name: '栏目管理',
          component: CateMana
        }
      ]
    },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
