<template>
<el-container>
  <el-row type="flex" class="view" justify="center">
    <el-col :span="20" style="width: 1200px;height: 100%;">
    <el-header class="show_header">
       <el-container style="height: 100px;width: 100%;">
         <el-image
         :src="url1"
          class="logo1">

         </el-image>
         <span class="logo_title">bobo_blog</span>
         <span class="header_title">
          波波君の蜗居<br>
          <span style="margin-left: 100px;font-size: 12px;">自己的饭量自己知道</span>
         </span>
       </el-container>


     <el-card shadow="always" body-style="padding:0;"
     style="width: 100%;height: 70px;background-color: #FFFFFF;border-radius: 10px;overflow: hidden;float: left;position: relative;">

       <el-container style="position: absolute;left: 15px;top: 10px;">
       <el-menu :default-active="activeIndex"  mode="horizontal" @select="handleSelect">
         <el-menu-item index="1"><a href="/"  style="text-decoration: none;"><i class="el-icon-price-tag el-icon--right" style="margin-bottom: 5px;"></i>首页</a></el-menu-item>
         <el-menu-item index="2">
           <el-dropdown trigger="click" placement="bottom-start" style="margin-top: -2px;margin-left: 10px;">
             <span class="el-dropdown-link">
               <i class="el-icon-arrow-down el-icon--right"></i>分类
             </span>
             <el-dropdown-menu slot="dropdown">
               <el-dropdown-item v-for="tag in categories" v-bind:key="tag.id">
                 <el-button type="text" @click="checkcate(tag.id)" class="tags" round v-bind:key="tag.id">{{tag.cateName}}</el-button>
                 </el-dropdown-item>
             </el-dropdown-menu>
           </el-dropdown>
         </el-menu-item>
         <el-menu-item index="3" style="margin-left: 30px;"><a href="https://space.bilibili.com/75079594/" style="text-decoration: none;"><i class="el-icon-video-camera-solid" style="margin-bottom: 5px;"></i>视频</a></el-menu-item>
       </el-menu>
       </el-container>

       <el-container style="position: absolute;right: 50px;top: 20px;">
       <el-input
         placeholder="请输入搜索的关键字..."
         v-model="keywords" style="width: 400px;" size="medium">
       </el-input>
       <el-button type="primary" icon="el-icon-search" size="mini" style="margin-left: 3px;height: 35px;" @click="searchClick">搜索
       </el-button>
       </el-container>

      </el-card>
     </el-header>


  <el-container style="width: 1200px;z-index: 2;height: 1200px;">
    <el-main style="width: 800px;position: relative;">
      <el-container style="height: 300px;width: 100%;overflow: hidden;position: relative;margin-bottom: 20px;">
        <el-image
        :src="url2"
        height="100%"
        width="100%"
        style="border-radius: 15px;">
        </el-image>
        <el-image
        :src="url3"
        style="position: absolute;left: 50%;top: 50%;transform: translate(-50%,-50%);"
>
        </el-image>
      </el-container>


    <el-container style="margin-bottom: 100px;width: 100%;display: block;">
      <template v-for="(item,index) in articles">
            <el-card shadow="always" v-bind:key="item.id" style="width: 100%;height: 105px;margin-bottom: 15px;position: relative;">
             <div slot="header" style="height: 30px;">
               <el-button  type="text" class="show_btn" @click="blogdetail(item.id)">{{item.title}}</el-button>
                <a class="el-icon-location" style="float: right;padding-left: 10px;">{{item.editTime | formatDateTime}}</a>
             </div>
             <div class="name">
             <span>作者 ：{{item.nickname}}  </span> <span>|</span>
             <span>分类 ：{{item.cateName}}  &emsp;</span><span>|</span>
             <span>浏览 ：{{item.pageView}}</span>
             </div>
            </el-card>
      </template>
      </el-container>
      <el-pagination
        background
        :page-size="pageSize"
        layout="prev, pager, next"
        :total="totalCount" @current-change="currentChange" v-show="this.articles.length>0"
        style="position: absolute;bottom: 55px;left: 350px;">
      </el-pagination>

    </el-main>

    <aside style="width: 300px;height: auto;padding-top: 30px;" >
          <el-card shadow="always" class="aside_card">
            <div slot="header" style="font-size: 15px;font-weight: bold;">当前文章统计</div>
            <div>
              <span style="position: absolute;left: 35px;top: 80px;font-size: 12px;">文章总数：{{totalCount}}</span>
              <span style="position: absolute;left: 155px;top: 80px;font-size: 12px;">分类总数：{{categories.length}}</span>
              <span style="position: absolute;left: 35px;top: 110px;font-size: 15px;">联系方式：1409009852@qq.com</span>
            </div>
          </el-card>
          <el-card shadow="always" class="aside_card">
            <div slot="header" style="font-size: 15px;font-weight: bold;">友情链接</div>
            <div style="display: block;color: #333333;">
              <el-button type="text" size="medium"><a href="https://space.bilibili.com/75079594/" style="text-decoration: none;">哔哩哔哩账号主页</a></el-button>
              <el-button type="text" size="medium"><a href="https://github.com/bobo502/java-learn" style="text-decoration: none;">Github账号主页</a></el-button>
            </div>
          </el-card>
          <el-card shadow="always" class="aside_card">
            <div slot="header" style="font-size: 15px;font-weight: bold;">感言</div>
            <div>
              <span style="font-size: 13px;">我不是一个特别特别耐心的人，但希望自己能够坚持下去，每天进步一点点...</span>
            </div>
          </el-card>
    </aside>
    </el-container>

    </el-col>
  </el-row>
</el-container>
</template>
<script>
  import {putRequest} from '../utils/api'
  import {getRequest} from '../utils/api'

  export default{
    data(){
      return{
        keywords:'',
        articles: [],
        loading: false,
        currentPage: 1,
        totalCount: -1,
        pageSize: 6,
        categories:[],
        url1:require("../assets/index/logo1.png"),
        url2:require("../assets/index/index2.jpg"),
        url3:require("../assets/index/index3.png"),
        activeIndex:'1'
      }
    },

    mounted:function(){
      var _this = this;
      this.loading = true;
      this.loadBlogs(1, this.pageSize);
      this.refresh();
    },
    methods:{
      handleSelect(){

      },
      blogdetail(id){
         this.$router.push({path: '/blogshowdetail', query: {aid: id}})
      },
      getallArticle(){
        this.loadBlogs(1, this.pageSize);
      },
      currentChange(currentPage){
        this.currentPage = currentPage;
        this.loading = true;
        this.loadBlogs(currentPage, this.pageSize);
      },
      searchClick(){
      this.loadBlogs(1, this.pageSize);
    },
      //向后台发送数据
      loadBlogs(page, count){
        var _this = this;
        var url = '';
        url = "/webshow/show_article?page=" + page + "&count=" + count + "&keywords=" + this.keywords;

        getRequest(url).then(resp=> {
          _this.loading = false;
          if (resp.status == 200) {
            _this.articles = resp.data.articles;
            _this.totalCount = resp.data.totalCount;
          } else {
            _this.$message({type: 'error', message: '数据加载失败!'});
          }
        }, resp=> {
          _this.loading = false;
          if (resp.response.status == 403) {
            _this.$message({type: 'error', message: resp.response.data});
          } else {
            _this.$message({type: 'error', message: '数据加载失败!'});
          }
        }).catch(resp=> {
          //压根没见到服务器
          _this.loading = false;
          _this.$message({type: 'error', message: '数据加载失败!'});
        });
      },
      //根据分类获取文章
      checkcate(cid){
        this.loadBlogsByCate(1,this.pageSize,cid);
      },
      loadBlogsByCate(page, count,cate){
        var _this = this;
        var url = '';
        url = "/webshow/show_articleByCate?page=" + page + "&count=" + count + "&cateid=" +cate+ "&keywords=" + this.keywords;

        getRequest(url).then(resp=> {
          _this.loading = false;
          if (resp.status == 200) {
            _this.articles = resp.data.articles;
            _this.totalCount = resp.data.totalCount;
          } else {
            _this.$message({type: 'error', message: '数据加载失败!'});
          }
        }, resp=> {
          _this.loading = false;
          if (resp.response.status == 403) {
            _this.$message({type: 'error', message: resp.response.data});
          } else {
            _this.$message({type: 'error', message: '数据加载失败!'});
          }
        }).catch(resp=> {
          //压根没见到服务器
          _this.loading = false;
          _this.$message({type: 'error', message: '数据加载失败!'});
        });
      },
      refresh(){
          let _this = this;
          getRequest("/webshow/catepory/all").then(resp=> {
            _this.categories = resp.data;
            _this.loading = false;
          }, resp=> {
            if (resp.response.status == 403) {
              _this.$message({
                type: 'error',
                message: resp.response.data
              });
            }
            _this.loading = false;
          });
        }
  }
}
</script>

<style scoped="scoped">
  a{
    color: #505458;
  }
  a:hover{
    color: #e67e22;
  }
  *{
        font: 'MicrosoftYaHei', '微软雅黑', Tahoma, Verdana;
    }

  .view{
    height: 100%;
    width: 100%;
    background-image: url(../assets/index/index7.jpg);

  }
  .show_header{
     height: 300px;
     width: 100%;
     position: relative;
     }
  .logo1{
    height: 50px;
    width: 50px;
    margin-top: 25px;
    transform-origin:0 0;
    transform:rotate(-15deg);

    }
    .logo_title{
      font-size: 25px;
      color: #f4ea2a;
      font-weight: bold;
      margin-top: 40px;
      margin-left: 5px;
    }
  .header_title{
       font-size: 25px;
       position: absolute;
       right: 50px;
       top: 20px;
     }

   .show_btn{
     font-size: 25px;
      color: #000;
      position: absolute;
      left: 2%;
      top: 6%;
   }
   .show_btn:hover{
     color: #2980b9;
   }
   .tags{
     width: 100%;
     height: 35%;
     padding: 10px 10px;
     font-size: 15px;
   }
   .name{
     float: right;
     color: #bdc3c7;
     margin: -10px 0px;
   }
   .name span{
     padding-left: 10px;
   }
  .aside_card{
    width: 100%;
    height: 150px;
    margin-bottom: 30px;
    padding: 0;
    position: relative;
  }

</style>
